import { createStore } from "@stencil/store";
import { ProductShortData } from "./product";

export const cart = createStore({
    "loading": 0,
    "loadingDeals": false,
} as CartData);

export interface CartData {
    api?: CartApi;

    currency?: string;
    totalValue?: number;
    productValue?: number;
    productAmount?: number;
    pointsForOrder?: number;
    totalShippingTime?: string;

    otherValues?: CartSummaryItem[];
    shippingProgress?: CartShippingProgress;

    products?: {[index: string]: CartProduct};

    productMessage?: {
        message: string;
        link: string;
    } | null;

    deals?: CartDeal[];
    dealGroups?: CartDealGroup[];

    countries?: CartCountry[];

    shipping?: CartSelectItem[];
    payment?: CartSelectItem[];

    shippingMessage?: string;
    paymentMessage?: string;

    activeShipping?: number;
    activePayment?: number;

    points?: CartPoints;
    discount?: CartDiscount;
    discountStrings?: CartDiscountStrings;

    loading?: number;
    loadingDeals?: boolean;

    recycle?: CartRecycle[];
    easyprotect?: CartEasyprotect;
    insured?: easyprotectInsured;
    easyprotectStrings?: CartEasyprotectStrings;
    strings?: CartStrings;

    progressBar?: boolean;

    noContentHeading?: string;
    noContentMessage?: string;
}

export interface CartApi {
    reload?: string;

    productRemove?: string;
    productCount?: string;
    addDeal?: string;

    countryChange?: string;
    shippingChange?: string;
    paymentChange?: string;

    discountCode?: string;
    discountPoints?: string;
    discountRemove?: string;

    easyprotectChange?: string;
    easyprotectRemove?: string;

    recycleChange?: string;

    order?: string;
}

export interface CartStrings {
    orderProgressCart: string;
    orderProgressShipping: string;
    orderProgressConfirmation: string;

    recycleHeading: string;
    recycleParagraph: string;

    countrySelectHeading: string;
    shippingName: string;
    shippingSelect: string;
    shippingMethodMissing: string;
    paymentName: string;
    paymentSelect: string;
    paymentMethodMissing: string;

    summaryProductValue: string;
    summaryTotalValue: string;
    submitButton: string;
    installmentsButton: string;

    countUnit: string;
    thresholdPrefix: string;
    addWarranty: string;
    selectColor: string;
    
    discountPointsLogin: string;
    discountPointsActivate: string;
    discountPointsHeading: string;
    discountCodeHeading: string;
    discountCodeActivate: string;

    productTableNames: string;
    productTablePrices: string;
    productTableCount: string;
    productTableRemove: string;
    productTableTotal: string;
}

export interface CartDiscount {
    heading: string;
    name: string;
    value: string;
}

export interface CartDiscountStrings {
    infoMessage: string;
                
    codeBanner: string;
    codePlaceholder: string;

    pointsPlaceholder: string;
    pointsMessage: string;
    pointsLoggedInMessage: string;
    noPointsHeading: string;
    noPointsMessage: string;
    thresholdHeading: string;
    thresholdMessage: string;
}

export interface CartCountry {
    selected: boolean;
    name: string;
    value: string;
}

export interface CartPoints {
    threshold: number;
    available: number;
    value: number;

    placeholder: string;
}

export interface CartSummaryItem {
    name: string;
    value: number;
}

export interface CartShippingProgress {
    heading: string;
    threshold: number;
    color: string;
}

export interface CartProduct extends ProductShortData {
    category: string; 
    warranty: string;
    maxQuantity: number;
    shippingTime: string;
}

export interface CartDeal {
    id: string;
    name: string;
    link: string;
    img: string;
    price: string;
    currentPrice: string;
    retailPrice: string;
    lowestPrice: string;
}

export interface CartDealGroup {
    name: string;
    deals: CartDeal[];
}

export interface CartSelectItem {
    id: number;
    logo: string;
    name: string;
    price?: number;
    color: string;
}

export interface CartRecycle {
    id: number;
    name: string;
    checked: boolean;
}

export interface CartEasyprotect {
    [index: string]: {
        [index: string]: number
    };
}

export type easyprotectInsured = {[index: string]: string};

export interface CartEasyprotectStrings {
    heading: string;
    description: string;
    image: string;
    width: number;
    height: number;
    pdf: string;
    seeMore: string;

    productsHeading: string;
    productsMessage: string;
    warrantyHeading: string;
    warrantyMessage: string;
}